body {
	font-family: 'Karla', sans-serif;
	background-image: url('../images/bg.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	color: #ffffff;
	padding-bottom: 75px;

	@media (min-width: 768px) {
		padding-bottom: 110px;
	}
}
.navbar-brand {
	margin-right: 0;

	img {
		height: 140px;
	}
}
.navbar-dark {
	padding-top: 35px;
	padding-bottom: 35px;
	.navbar-text {
		color: #ffffff;
		opacity: 1;
		padding: 0 20px;
		border-right: 1px solid #ffffff;
		font-size: 18px;
		font-weight: bold;
		text-align: center;

		&:last-child {
			border-right: 0;
		}
	}

	@media (max-width: 767px) {
		padding-bottom: 0;
	}
}
.page-title {
	font-size: 80px;
	font-weight: bold;
	margin-bottom: 70px;

	@media (max-width: 767px) {
		font-size: 38px;
		font-weight: bold;
		margin-bottom: 104px;
	}
}
.page-description {
	max-width: 465px;
	font-size: 18px;
	margin-bottom: 59px;

	@media (max-width: 767px) {
		font-size: 14px;
	}
}

p {
	font-size: 14px;
	margin-bottom: 21px;
}
